<template>
	<view class="home">
		<navbar :isSerach="true" v-model="value" @input="change"></navbar>
		<view class="home-list">
			<view class="label-box" v-if="history">
				<view class="label-header">
					<text class="label-title">搜索历史</text>
					<text class="label-clear" @click="clear">清空</text>
				</view>
				<view v-show="historyList.length > 0" class="label-content">
					<view class="label-content__item" v-for="(item,index) in historyList" :key="index"
						@click="openHistory(item)">
						{{item.name}}
					</view>
				</view>
				<view v-show="historyList.length <= 0" class="no-data">
					没有搜索历史
				</view>
			</view>
			<list-scroll v-else class="list-scroll">
				<uni-load-more v-if="loading" status="loading" iconType="snow"></uni-load-more>
				<view v-if="searchList.length > 0">
					<list-card :item="item" @click="setHistory" v-for="item in searchList" :key="item._id"></list-card>
				</view>
				<view v-if="searchList.length === 0 && !loading" class="no-data">
					没有搜索到相关数据
				</view>
			</list-scroll>
		</view>
	</view>
</template>

<script>
	import {
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				value: '',
				history: true,
				searchList: [],
				loading: false
			}
		},
		computed: {
			...mapState(['historyList'])
		},
		methods: {
			openHistory(item) {
				this.value = item.name
				this.getSearch(this.value)
			},
			setHistory() {
				this.$store.dispatch('set_history', {
					name: this.value
				})
			},
			change(value) {
				if (!value) {
					clearTimeout(this.timer);
					this.mark = false
					this.getSearch(value)
					return
				}
				//做标记
				if (!this.mark) {
					this.mark = true;
					this.timer = setTimeout(() => {
						this.mark = false
						this.getSearch(value)
					}, 1000)
				}
			},
			testBtn() {
				this.$store.dispatch('set_history', {
					name: 'test'
				})
			},
			getSearch(value) {
				if (!value) {
					this.searchList = [];
					this.history = true;
					return
				}
				this.history = false
				this.loading = true
				this.$api.get_search({
					value: value
				}).then(res => {
					const {
						data
					} = res;
					this.loading = false
					this.searchList = data
				}).catch(()=>{
					this.loading = false
				})
			},
			clear(){
				this.$store.dispatch('clearHistory')
				uni.showToast({
					title:'清空完成'
				})
			}
		}
	}
</script>

<style lang="scss">
	page {
		height: 100%;
		display: flex;
		background-color: #F5F5F5;
	}

	.home {
		display: flex;
		flex-direction: column;
		flex: 1;
		.label-box {
			background-color: $uni-bg-color;
			margin-bottom: 10px;

			.label-header {
				display: flex;
				justify-content: space-between;
				font-size: 14px;
				color: #666666;
				padding: 10px 15px;
				border-bottom: 1px solid #F5F5F5;

				.label-title {
					color: $mk-base-color;
				}

				.label-clear {
					color: #30b33a;
					font-weight: bold;
				}
			}

			.label-content {
				display: flex;
				flex-wrap: wrap;
				padding: 15px;
				padding-top: 0;

				.label-content__item {
					padding: 2px 10px;
					margin-top: 12px;
					margin-right: 12px;
					border-radius: 5px;
					border: 1px solid #666666;
					font-size: 14px;
					color: #666666;
				}
			}
		}
	}

	.no-data {
		height: 200px;
		line-height: 200px;
		width: 100%;
		text-align: center;
		font-size: 12px;
		color: #666666;
	}
</style>
